<template>
    <div id="app">
        <div class="insertMap-contentbox" :style="{paddingBottom: lang == 'zh' ? '70px' : ''}">
            <div class="map-topbar">
                <img :src="require('@/assets/img/logo.svg')" @click="openUrl('https://www.naturalearthdata.com')" style="cursor: pointer" alt="">
                <div class="map-topbar-search">
                    <img class="map-search-icon" :src="require('@/assets/img/search-icon.svg')" @click="clickSearch" alt="">
                    <input ref="input" v-model="searchValue" :placeholder="$t('searchMapTips')+'...'" class="map-search" @input="matchMap()" @focus="matchMap" @blur="matchList = []"  @keyup.enter="enterSearchValue" />

                    <div class="search-list">
                        <div class="search-list-item" v-for="(item, index) in matchList" :key="index" @mousedown="selectSearchValue(item)">{{getName(item.name)}}</div>
                    </div>
                </div>
            </div>
            <div class="map-label-box">
                
                
                <div class="map-label">
                    <div class="map-category">
                        <div 
                            :class="{'active-category': categoryIndex == index}" 
                            v-for="(item, index) in mapData" 
                            :key="index"
                            v-show="item.state"
                            @click="selectCategory(index)">{{ item.name }}</div>
                    </div>
                    <div class="map-category-2">
                        <div 
                            :class="{'active-category-2': category2Index == index, 'category-2-sub': item.type == 'initial'}" 
                            v-for="(item, index) in mapData[categoryIndex].child" 
                            :key="index" 
                            @dblclick="selectCategory2(index, item),insertChart()"
                            @click="selectCategory2(index, item)">{{getName(item.name)}}</div>
                    </div>
                    <div class="map-noresult" v-show="noSearchResult">
                        <img :src="require('@/assets/img/noresult.svg')" alt="">
                        <p>{{ lang == 'zh' ? '没有相关结果...' : 'No Result' }}</p>
                    </div>
                </div>
            </div>
            
            <div class="map-box" v-if="false">
                <div class="map-name">{{getName(mapData[categoryIndex].child[category2Index].name)}}</div>
                <div id="map-view"></div>
            </div>
        </div>

        <div class="map-loading" v-if="showLoading">
            <img src="@/assets/img/logoanimation.gif" alt="">
        </div>

        <div class="map-naturalText">
            <p @click="openLink($event)"><span class="link_section_text" v-html="$t('tips1')"></span></p>
            <p><span class="link_section_text" v-text="$t('tips2')"></span></p>
        </div>
        <div class="dialog-btnbox-normal">
            <div class="dialog-btn-ok" @click="insertChart()">{{$t('confirm')}}</div>
            <div class=""  @click="cancelInsert()">{{$t('cancel')}}</div>
        </div>
    </div>
</template>

<script>
import axios from 'axios'
import untar from 'js-untar'
import Qtweb from 'qwebchannel'


export default {
    name: "Map",
    components: {
        
    },
    data(){
        return {
            lang: 'zh',
            categoryIndex: 0,
            category2Index: 0,
            searchValue: '',
            matchList: [],
            mapData: [
                {
                    name: '世界',
                    child: world,
                    state: true
                },
                {
                    name: '国家',
                    child: countrys,
                    state: true
                },
                {
                    name: '中国',
                    child: china,
                    state: false
                }
            ],

            mapName: {},
            showLoading: true,
            myChart:  null ,
            eChartData: {},
            internal: 0,
            noSearchResult: false,

        }
    },
    methods:{
        getEChartsData(){
            let geoDataFolder = ['geo','world', 'china'];
            return new Promise((resolve)=>{
                let opendb = window.indexedDB.open('EChartsData');
                opendb.onsuccess = ()=>{
                    let db = opendb.result;
                    let allDataArr =[];
                    for(let i  =0; i < geoDataFolder.length; i++){
                        let tbName = geoDataFolder[i];
                        this.eChartData[tbName] = {}
                        let trans = db.transaction([tbName], 'readonly');
                        let store = trans.objectStore(tbName);
                        let cursorReq = store.openCursor();
                        let pro = new Promise((res)=>{
                            cursorReq.onsuccess = ()=>{
                                let cursor = cursorReq.result;
                                if(cursor){
                                    let key = cursor.key;
                                    let data = cursor.value;
                                    this.eChartData[tbName][key] = data.data;
                                    cursor.continue();
                                }else{
                                    res();
                                }
                            }
                        })
                        allDataArr.push(pro);
                        Promise.all(allDataArr).then(()=>{
                            resolve();
                        })
                    }
                    
                }
            })
        },
        initData(){
            let loadingEnd = (ok) => { // 加载完成后打开弹窗
                // 地图注册
                this.getEChartsData().then(e =>{
                    let MapData = this.eChartData
                    for (let i in MapData) {
                        for (let key in MapData[i]) {
                            echarts.registerMap(key, MapData[i][key])
                        }
                    }
                    this.showLoading = false;
                    ok();
                })
            }
            let storageFuc = (type, name = "EChartVersion") => {
                if(type == 'set'){
                    localStorage.setItem(name, '1');
                }else if(type == 'get'){
                    return localStorage.getItem(name);
                }
            }
            let addDataToDB = () => {
                return new Promise((addOver) => {
                    let readJSON = (file, tableObj, name) => {
                        let fr = new FileReader();
                        fr.readAsText(file);
                        let promise = new Promise((res)=>{
                            fr.onload = (e) => {
                                tableObj[name] = {
                                    name: name,
                                    data:JSON.parse(e.target.result)
                                }
                                res()
                            }
                        })
                        return promise
                    }
                    axios.get('./EChartsData.tar', {responseType: "arraybuffer" }).then(res => {
                        untar(res.data).then((arr)=>{
                            console.log(arr)
                            let dbName = ''; // 数据库名
                            let dbData = {};
                            let allInitArr = [];
                            dbName = arr[0].name.slice(0,-1);
                            dbData[dbName] = {}; //dbData 第一层 dbname。数据库名。
                            // let dbNameLen = arr[0].name.length;
                            let len = arr.length;
                            let tbNameList = [];

                            for (let i = 1; i < len; i++) {
                                if(/\/$/.test(arr[i].name)){ // /结尾，文件夹文件
                                    let tbName = arr[i].name.match(/[^\/]*.(?=\/$)/)[0];
                                    dbData[dbName][tbName]={};
                                    tbNameList.push(tbName);
                                }else{
                                    let tbName = arr[i].name.match(/[^\/]*(?=\/.[^\/]*\.json)/)[0]
                                    let json = new Blob([arr[i].buffer]);
                                    if(!dbData[dbName][tbName]){
                                        dbData[dbName][tbName] = {};
                                        tbNameList.push(tbName);
                                    }
                                    let name = arr[i].name.match(/[^\/]*(?=\.json)/)[0];
                                    let jsonPro = readJSON(json, dbData[dbName][tbName], name);
                                    allInitArr.push(jsonPro)
                                }
                            }

                            Promise.all(allInitArr).then(()=>{
                                let opendb = window.indexedDB.open(dbName, 1);
                                let tbNameLen = tbNameList.length;
                                let db = null;
                                opendb.onupgradeneeded = () => { // 创建数据表
                                    db = opendb.result;
                                    let key = 'name';
                                    for (let i = 0; i < tbNameLen; i++) {
                                        let tbName = tbNameList[i];
                                        if(!db.objectStoreNames.contains(tbName)){
                                            db.createObjectStore(tbName, { keyPath: key });
                                        }
                                    }
                                }

                                opendb.onsuccess = () => {
                                    db = opendb.result;
                                    let allAddTbArr = [];
                                    for (let i = 0; i < tbNameLen; i++) {
                                        let tbName = tbNameList[i];
                                        let tbData = dbData[dbName][tbName];
                                        let trans = db.transaction([tbName],'readwrite');
                                        let objStore = trans.objectStore(tbName);

                                        let data = Object.keys(tbData);
                                        for (let j = 0; j < data.length; j++) {
                                            objStore.add(tbData[data[j]]);
                                        }
                                        
                                        
                                        allAddTbArr.push(new Promise((done) => {
                                            trans.oncomplete = () => {
                                                done();
                                            }
                                        }))
                                    }
                                    Promise.all(allAddTbArr).then(()=>{
                                        addOver()
                                        db.close();
                                    })
                                }
                            })
                        })
                    })
                })
            }
            
            return new Promise((ok) => {
                if(storageFuc('get') != '1'){ // 判断是否加载过
                    console.log('unloaded')
                    addDataToDB().then(() => {
                        storageFuc('set');
                        loadingEnd(ok);
                    });
                }else{
                    console.log('loaded')
                    loadingEnd(ok);
                }
            })
        },

        setMap(item) {
            return
            this.myChart.setOption({geo: { map: item.name, silent:true }})
        },

        matchMap () {
            this.noSearchResult = false;
            let matchList = []
            for (let key in mapHanzi) {
                if (mapHanzi[key].includes(this.searchValue)) {
                    this.mapData[2].child.forEach((item, index) => {
                        if (item.name == key) {
                            let item = {
                                categoryIndex: 2,
                                category2Index: index,
                                name: key
                            }
                            matchList.push(item)
                        }
                    })
                }
            }
            for (let key in mapCountry) {
                if (mapCountry[key].includes(this.searchValue)) {
                    this.mapData[1].child.forEach((item, index) => {
                        if (item.name == key) {
                            let item = {
                                categoryIndex: 1,
                                category2Index: index,
                                name: key
                            }
                            matchList.push(item)
                        }
                    })
                }
            }
            let worldMap = new Map([
                ["World", "世界"],
                ["Asia", "亚洲"],
                ["Africa", "非洲"],
                ["Antarctica", "南极洲"],
                ["Europe", "欧洲"],
                ["Oceania", "大洋洲"],
                ["South america", "南美洲"],
                ["North america", "北美洲"]
            ])
            this.mapData.forEach((item, index) => {
                item.child.forEach((map, index2) => {
                    if (map.type != 'initial') {
                        let re = new RegExp(this.searchValue, "ig")
                        let name = worldMap.get(map.name) || '';
                        if (map.name.match(re) || name.indexOf(this.searchValue) != -1) {
                            let item = {
                                categoryIndex: index,
                                category2Index: index2,
                                name: map.name
                            }
                            matchList.push(item)
                        }
                    }
                })
            })
            this.matchList = matchList
        },

        clickSearch() {
            let matchList = []
            for (let key in mapHanzi) {
                if (mapHanzi[key].includes(this.searchValue)) {
                    this.mapData[2].child.forEach((item, index) => {
                        if (item.name == key) {
                            let item = {
                                categoryIndex: 2,
                                category2Index: index,
                                name: key
                            }
                            matchList.push(item)
                        }
                    })
                }
            }
            for (let key in mapCountry) {
                if (mapCountry[key].includes(this.searchValue)) {
                    this.mapData[1].child.forEach((item, index) => {
                        if (item.name == key) {
                            let item = {
                                categoryIndex: 1,
                                category2Index: index,
                                name: key
                            }
                            matchList.push(item)
                        }
                    })
                }
            }
            let worldMap = new Map([
                ["World", "世界"],
                ["Asia", "亚洲"],
                ["Africa", "非洲"],
                ["Antarctica", "南极洲"],
                ["Europe", "欧洲"],
                ["Oceania", "大洋洲"],
                ["South america", "南美洲"],
                ["North america", "北美洲"]
            ])
            this.mapData.forEach((item, index) => {
                item.child.forEach((map, index2) => {
                    if (map.type != 'initial') {
                        let re = new RegExp(this.searchValue, "ig")
                        let name = worldMap.get(map.name) || '';
                        if (map.name.match(re) || name.indexOf(this.searchValue) != -1) {
                            let item = {
                                categoryIndex: index,
                                category2Index: index2,
                                name: map.name
                            }
                            matchList.push(item)
                        }
                    }
                })
            })
            let select = matchList.find(item=>this.searchValue === this.getName(item.name));
            if(select){
                this.selectSearchValue(select);
                this.$refs.input.blur();
            } else {
                this.noSearchResult = true;
            }
        },

        selectSearchValue (item) {
            this.noSearchResult = false;
            this.categoryIndex = item.categoryIndex
            this.category2Index = item.category2Index
            this.$nextTick(() => {
                this.jumpToSelect()
            })
            this.setMap(item)
        },

        enterSearchValue(){
            let select = this.matchList.find(item=>this.searchValue === this.getName(item.name));
            if(select){
                this.selectSearchValue(select);
                this.$refs.input.blur();
            } else {
                this.noSearchResult = true;
            }
        },

        getName (name) {
            if(this.lang != 'zh')
                return name
            if (mapHanzi[name]) {
                return mapHanzi[name]
            } 
            if(mapCountry[name]){
                return mapCountry[name]
            }
            else if (this.mapName && this.mapName[name]) {
                return this.mapName[name]
            }
            return name
        },

        selectCategory (index) {
            this.categoryIndex = index;
            this.noSearchResult = false;
            if (index == 0) {
                this.category2Index = 0
                this.setMap(this.mapData[0].child[0])
            } else {
                this.category2Index = 1
                this.setMap(this.mapData[index].child[1])
            }
        },

        selectCategory2 (index, item) {
            if (item.type != 'initial') {
                this.category2Index = index
                this.setMap(item)
            }
        },
        
        jumpToSelect(){
            setTimeout(() => {
                let dom = document.querySelector('.active-category-2');
                if(dom){
                    dom.scrollIntoView({behavior: 'smooth', block: 'center'})
                }
            }, 0);
        },
        
        insertChart(){
            let nameList = ['world', 'geo', 'china'];
            let obj = {
                kind: nameList[this.categoryIndex],
                mapName: this.mapData[this.categoryIndex].child[this.category2Index].name
            }

            window.qtsdk.jsCallInsertMap(JSON.stringify(obj));
        },
        cancelInsert(){
            window.qtsdk.jsCallCloseDialog();
        },
        openLink(e){
            if(e.target.tagName == "A"){
                let url = e.target.getAttribute('data-url');
                this.openUrl(url)
            }
        },
        openUrl(url){
            window.qtsdk.jsCallOpenUrl(url);
        },

        dblclick () {
            this.insertChart()
        },
    },
    mounted() {
        // this.initData().then(() => {
            // let mapView = document.querySelector('#map-view')
            // this.myChart = echarts.init(mapView)
            this.setMap({type: 'world', name: 'World'})
            let lang = this.lang
            this.mapData = [
                {
                    name: this.$t('world').toString(),
                    child: world,
                    state: true
                },
                {
                    name: this.$t('country').toString(),
                    child: countrys,
                    state: true
                },
                {
                    name: this.$t('china').toString(),
                    child: china,
                    state: false
                }
            ]
            if(this.internal){
                if (lang == 'zh') {
                    this.mapData[2].state = true
                }
            }
            this.mapName = this.$t('mapName')
            this.showLoading = false;
        // })
    },
    created(){
        window.addEventListener('keydown', (e) => {
            if(e.keyCode == 27) {
                window.qtsdk.jsCallCloseDialog && window.qtsdk.jsCallCloseDialog();
            }
        })
        let urlParams = new URLSearchParams(location.search);
        let lang = urlParams.get('lang');
        this.internal = urlParams.get('internal');
        let theme = urlParams.get('theme') || 0;
        let maxColorList = ['#0977ea', '#3a4275', '#4f5359']
        if(!['zh', 'en'].includes(lang)){
            lang = 'en';
        }
        // lang="zh"
        this.lang = lang;

        document.body.style.setProperty('--themeColor', maxColorList[theme])
        this.$i18n.locale = lang;

        if(typeof window.qtsdk == 'undefined') {
            let QWebChannel = Qtweb.QWebChannel;
            new QWebChannel(qt.webChannelTransport, function (channel) {
                window.qtsdk = channel.objects.qtsdk;
            });
        }
    }
};
</script>

