import Vue from 'vue'
import VueI18n from "vue-i18n"
import App from './App.vue'

Vue.config.productionTip = false
Vue.use(VueI18n)

let urlParams = new URLSearchParams(location.search);
let list = [];
if(urlParams.get('lang') == 'zh') {
  list.push(new Promise((rs, rj) => {
    import('@/assets/js/map.js').then((module) => {
        console.log(module)
        let { world, countrys, mapCountry, mapHanzi, china } = module;
        window.world = world;
        window.countrys = countrys;
        window.mapCountry = mapCountry;
        window.mapHanzi = mapHanzi;
        window.china = china;
        rs();
    })
  }))
} else {
  list.push(new Promise((rs, rj) => {
    import('@/assets/js/map-en.js').then((module) => {
        console.log(module)
        let { world, countrys, mapCountry, mapHanzi, china } = module;
        window.world = world;
        window.countrys = countrys;
        window.mapCountry = mapCountry;
        window.mapHanzi = mapHanzi;
        window.china = china;
        rs();
    })
  }))
}

const i18n = new VueI18n({
	locale: "zh", // set locale
	messages: {}, // set locale messages
    silentTranslationWarn: true,
    fallbackLocale:"en"
});
Promise.all(list).then(() => {
  window.app = new Vue({
    i18n,
    render: h => h(App),
  }).$mount('#app')  
})
